ul{
    list-style-type: none;
    display: flex;
    li{
        margin: 0 15px;
        a{
            color: #fff;
            text-decoration: none;
            font-size: 28px;
            &:hover{
                color: rgb(13, 168, 207);
                text-decoration: underline;
                
            }
        }
        
    }
}
.header-container{
    width: 80%;
    margin: 0 auto;
    display: flex;
    justify-content: space-between;
    align-items: center;
    height: 20vh;
    .icon{
        width: 50px;
        height: 50px;
    }
}
